
.page-title__demo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-items: space-between;
  height: 10vh;
  margin-left: -5px;
  margin-bottom: 1rem;
}
.page-title__titles {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.page-title__padding {
  height: 100%;
  margin-left: 5px;
  color: var(--content-dimmed-color);
  background-color: var(--gray-border-color);
  border-radius: var(--box-border-radius);
  min-width: 4%;
}
.page-title__title, .page-title__subtitle {
  color: #777;
  font-weight: bold;
  font-size: 115%;
  margin-bottom: 1rem;
  text-align: center;
  text-transform: uppercase;
}
.page-title__subtitle {
  font-size: 105%;
}



