.modal.is-active {
  display: block;
  opacity: 1;
}
.modal.is-active .modal-dialog {
  transform: translate(0, 0);
}
.editor-catalog {
}
.editor-catalog__items {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.editor-catalog__item {
  background-color: #e9e9e9;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
}

.editor-catalog__item__title {
  text-transform: uppercase;
  color: #555;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  width: 100%;
}

.editor-catalog__item__description {
  width: 100%;
  color: #999;
  font-size: 90%;
  margin-bottom: 0.5rem;
  min-height: 5rem;
  max-height: 10rem;
  overflow-y: auto;
}
.editor-catalog__item__select {
}



