
.image__preview {
  width: 100%;
}
.image__list-images {
  margin-top: 1rem;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0.5rem;
  max-height: 35vh;
  overflow-y: auto;
}
.image__list-image {
  border: 5px solid transparent;
  width: 100%;
}
.image__list-image:hover {
  border-color: var(--highlight-border-color);
}
.image__list-image--selected {
  border-color: #5cb85c;
}



